:root {
  --font-main: "Raleway", sans-serif;
  --font-secondary: "Reenie Beanie", cursive;
  --color-body: #c1766a;
  --color-main: #816461;
  --color-text: #fdfbec;
  --color-select: #b98b82;
  --color-input: #efb79a;
  --color-search-btn: #fae9d3;
  --color-outline: #fff6;
  --color-error: #e5383b4d;
  --color-error-outline: #e5383b33;
  --shadow: 0px 0px 15px 0px #444;
}

*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (max-width: 43.75em) {
  html {
    font-size: 50%;
  }
}

body {
  background: var(--color-body);
  color: var(--color-text);
  font-family: var(--font-main);
  grid-template-columns: minmax(10%, 1fr) minmax(min-content, 180rem) minmax(10%, 1fr);
  grid-auto-rows: min-content;
  font-size: 1.8rem;
  font-weight: 400;
  display: grid;
}

@media only screen and (max-width: 43.75em) {
  body {
    grid-template-columns: 0% 1fr 0%;
  }
}

body .container {
  min-height: 100vh;
  background: var(--color-main);
  box-shadow: var(--shadow);
  grid-column: 2;
  grid-template-columns: 10% 1fr 10%;
  grid-auto-rows: min-content;
  row-gap: 5rem;
  padding-top: 4rem;
  display: grid;
}

body .container > * {
  grid-column: 2;
}

body .container select, body .container input, body .container button {
  cursor: pointer;
  font: inherit;
  border: none;
  transition: all .2s;
}

body .container select:hover, body .container input:hover, body .container button:hover {
  opacity: .7;
}

@media only screen and (max-width: 43.75em) {
  body .container {
    grid-template-columns: 5% 1fr 5%;
  }
}

header {
  font-family: var(--font-secondary);
}

header .main-heading {
  letter-spacing: 10px;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 1rem;
  font-size: 5rem;
  line-height: 4rem;
}

header .main-heading span {
  letter-spacing: 0;
  text-transform: lowercase;
  font-size: 4rem;
  display: block;
}

header .quote-author {
  text-align: end;
  margin-right: 20%;
  font-size: 3rem;
}

.search {
  flex-wrap: wrap;
  justify-content: center;
  gap: 3rem 2rem;
  padding: 5rem 0;
  display: flex;
}

.search__options, .search__input, .search__button {
  height: 5rem;
  text-align: center;
  color: var(--color-text);
  border-radius: 10rem;
  flex-basis: 28rem;
  padding: 0 1rem;
}

.search__options:focus, .search__input:focus, .search__button:focus {
  outline: 5px solid var(--color-outline);
  transform: translateY(-1rem);
}

.search__options {
  background: var(--color-select);
}

.search__input {
  background: var(--color-input);
}

.search__input::placeholder {
  color: inherit;
}

.search__input:focus::placeholder {
  opacity: .5;
}

.search__button {
  background: var(--color-search-btn);
  color: var(--color-body);
}

.main {
  min-height: 30rem;
  text-align: center;
  row-gap: 4rem;
  display: grid;
}

.main .placeholder {
  text-align: center;
}

.main .countries {
  gap: 4rem;
  display: grid;
}

.main .countries .country {
  border-radius: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-auto-rows: min-content;
  gap: 3rem 2rem;
  padding: 4rem;
  transition: all .2s;
  display: grid;
  box-shadow: 1px 1px 90px 2px #555;
}

.main .countries .country:hover {
  transform: scale(1.1);
}

@media only screen and (max-width: 43.75em) {
  .main .countries .country {
    padding: 2rem;
  }
}

.main .countries .country .country__data {
  gap: 2rem;
  display: grid;
}

.main .countries .country .country__data .c-name {
  font-family: var(--font-secondary);
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 3rem;
  font-weight: 600;
}

.main .countries .country .country__data h3 {
  font: inherit;
}

.main .countries .country .country__data span {
  color: var(--color-input);
  font-weight: 600;
}

.main .countries .country .country__images {
  place-content: center;
  justify-items: center;
  gap: 2rem;
  display: grid;
}

.main .countries .country .country__images .coa {
  width: auto;
  height: 15rem;
}

.main .countries .country .country__images .coa .not-found {
  width: 100%;
  height: 100%;
  align-items: center;
  display: flex;
}

.main .countries .country .country__images .coa img {
  width: 100%;
  height: 100%;
}

.main .countries .country .country__images .flag {
  width: auto;
  height: 20rem;
}

.main .countries .country .country__images .flag img {
  width: 100%;
  height: 100%;
}

.loader {
  width: 4.8rem;
  height: 4.8rem;
  border-top: 3px solid #fff;
  border-right: 3px solid #0000;
  border-radius: 50%;
  justify-self: center;
  margin-top: 5rem;
  animation: 1s linear infinite rotation;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.result span {
  background: var(--color-body);
  border-radius: 1rem;
  padding: 1rem 2rem;
  font-weight: 600;
}

.pagination {
  justify-content: center;
  align-items: center;
  gap: 7rem;
  display: flex;
}

.pagination button {
  color: var(--color-input);
  background: none;
  border-radius: 2rem;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 0 1rem;
  display: flex;
}

.pagination button svg {
  fill: var(--color-input);
  scale: .8;
}

.pagination button svg:first-child {
  rotate: 180deg;
}

.error {
  background: var(--color-error);
  color: var(--color-text);
}

.error:focus {
  outline: 5px solid var(--color-error-outline);
}

.error-msg {
  text-align: center;
}

.author {
  font-family: var(--font-secondary);
  text-transform: uppercase;
  justify-self: end;
  padding: 4rem 0;
  font-size: 2rem;
}

.author a:link, .author a:visited {
  color: inherit;
  text-decoration: none;
}

